.footer {
  background-color: var(--footer-bg-color);

  &__general {
    padding-top: 28px;
    padding-bottom: 28px;

    @include tablet {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    align-items: center;

    font-weight: 400;
    font-size: 14px;
    line-height: calc(16 / 14);

    color: var(--footer-text-color);

    @include tablet {
      flex-direction: row;
      justify-content: center;
    }
  }

  &__first {
    margin-bottom: 10px;

    @include tablet {
      margin-bottom: 0px;
    }
  }

  &__second {
    display: flex;
    gap: 3px;
    align-items: center;

    &-hero {
      display: flex;
      gap: 3px;
      align-items: center;

      text-decoration: underline;
      cursor: pointer;

      &-color {
        fill: var(--accent-color);
      }

      &:hover,
      &:focus {
        transform: scale(1.1);
        color: var(--accent-color);
      }
    }

    @include tablet {
      margin-left: 3px;
    }
  }
}
