$border-radius: 5px;

//  COLORS \\
$main-bg-color: #ffffff;
$footer-bg-color: #f7f7f7;
$light-color: #ffffff;
$dark-color: #000000;
$modal-text: #8c8c8c;
$accent-color: #ff6b01;
$red-color: #ff001b;
$hover-btn-color: #fec69e;
$trending-switcher-text: #b4b4b4;
$upper-btn-backg-color: #f8f8f8;

$bezier-hov: cubic-bezier(0.12, 0.68, 0.82, 0.55);

// OPACITY\\
$opacity-header: rgba(0, 0, 0, 0.56);

// BTN HOVER/FOCUS\\
$shadow: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
$shadow-header: 0px 4px 10px rgba(0, 0, 0, 0.5);
$shadow-button: 0px 8px 43px rgba(254, 198, 158, 0.6);
$btn-hover-bg: rgba(255, 107, 1, 0.5);
$filter-button-shadow: inset 2px 2px 2px #0000004d;
$movie-card-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.2);

$animationTransition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

// FONTS\\
$primary-font-family: 'Roboto', sans-serif;

// WIDTH\\
$mobile: 480px;
$min-tablet: 767px;
$tablet: 768px;
$min-desktop: 1279px;
$desktop: 1280px;
